#custom_policy_editor {
  margin: 0;
  width: 65em;
}
#custom_policy_editor p {
  font-size: 110%;
  margin: 1em;
}
#custom_policy_editor .CodeMirror {
  border-top: 1px #DDD solid;
  border-bottom: 1px #DDD solid;
}
#page_authentication {
  /**
   * The API tokens configuration.
   *
   * Structure:
   *     <div classs="rb-c-config-api-tokens">
   *      <div class="rb-c-config-api-tokens__main">
   *       <div class="rb-c-config-api-tokens__value">
   *        <input readonly="readonly" value="...">
   *       </div>
   *       <span class="fa fa-clipboard js-copy-token" title="Copy to clipboard"></span>
   *      </div>
   *      <div class="rb-c-config-api-tokens__info">
   *       ...
   *      </div>
   *      <div class="rb-c-config-api-tokens__actions"></div>
   *      <span class="rb-c-config-api-tokens__note"></span>
   *     </div>
   */
}
#page_authentication .rb-c-config-api-tokens__main {
  align-items: center;
  display: flex;
  white-space: normal;
}
#page_authentication .rb-c-config-api-tokens__actions {
  white-space: nowrap;
}
#page_authentication .rb-c-config-api-tokens__value {
  font-family: monospace;
  font-size: 120%;
  flex-grow: 1;
  margin-right: 1em;
}
#page_authentication .rb-c-config-api-tokens__value input {
  font-family: monospace;
  text-overflow: ellipsis;
  width: 100%;
}
#page_authentication .rb-c-config-api-tokens__value input:not(:hover) {
  border-color: transparent;
}
#page_authentication .rb-c-config-api-tokens__note {
  color: #888;
}
#page_authentication .rb-c-config-api-tokens__note.empty {
  color: #CCC;
  font-style: italic;
}
#page_authentication .rb-c-config-api-tokens .js-copy-token {
  font-size: 120%;
  cursor: pointer;
}
#page_authentication .rb-c-config-api-tokens .config-forms-list-action-label-policy-custom {
  color: blue;
  cursor: pointer;
  text-decoration: underline;
}
#page_authentication .rb-c-config-api-tokens .config-forms-list-action-policy {
  text-align: right;
}
#page_authentication .rb-c-config-api-tokens .rb-icon {
  vertical-align: middle;
}
#page_groups .config-group-display-name {
  color: #A0A0A0;
  font-size: 90%;
}
#page_groups .config-group-name {
  display: inline-block;
  min-width: 15em;
  padding-right: 2em;
}
#page_groups .config-forms-list-action-join,
#page_groups .config-forms-list-action-leave {
  min-width: 3em;
}
#page_oauth2 .app-entry-wrapper {
  overflow: hidden;
}
#page_oauth2 .config-app-name.disabled a {
  font-style: italic;
}
#page_oauth2 .config-app-name .rb-icon {
  vertical-align: middle;
}
#page_oauth2 .config-app-name a {
  vertical-align: middle;
}
#page_oauth2 .disabled-warning {
  margin-bottom: 1em;
}
#page_oauth2 p {
  line-height: normal;
}
.oauth-add-app {
  margin-top: 1em;
}
.edit-oauth2-app #id_redirect_uris_container li {
  padding: 0.25em 0;
}
.edit-oauth2-app .box-main {
  padding: 0;
}
.edit-oauth2-app .list-edit-widget ul {
